import * as XLSX from "xlsx";
import config from "./config.json";

const validateFile = (file, collabSpaceTitles) => {
  return new Promise((resolve, reject) => {
    console.log("Starting file validation for:", file.name);
    console.log(
      "[validateFile.js] Required Collaborative Space:",
      collabSpaceTitles
    );

    // üîç Ensure collabSpaceTitles is an array
    if (!Array.isArray(collabSpaceTitles)) {
      console.error(
        "[validateFile.js] ‚ùå collabSpaceTitles is not an array!",
        collabSpaceTitles
      );
      reject({
        errors: ["Internal error: Invalid Collaborative Space list."],
        headers: [],
      });
      return;
    }

    console.log(
      "[validateFile.js] Required Collaborative Spaces:",
      collabSpaceTitles
    );

    const reader = new FileReader();
    reader.readAsBinaryString(file);

    reader.onload = (e) => {
      console.log("File successfully read. Parsing...");

      const workbook = XLSX.read(e.target.result, { type: "binary" });
      const sheetName = workbook.SheetNames[0];
      console.log("Detected sheet name:", sheetName);

      const worksheet = workbook.Sheets[sheetName];

      const headers =
        XLSX.utils.sheet_to_json(worksheet, { header: 1 })[0] || [];
      console.log("Extracted headers:", headers);

      if (!headers || headers.length === 0) {
        console.error("‚ùå No headers found in the file.");
        reject({
          errors: ["The uploaded file has no content or is empty."],
          headers: [],
        });
        return;
      }

      // ‚úÖ Convert worksheet into JSON (with empty cell values captured)
      const sheetData = XLSX.utils.sheet_to_json(worksheet, { defval: "" });
      console.log("Extracted sheet data:", sheetData);

      if (sheetData.length === 0) {
        console.error("‚ùå No row data found. Only headers exist.");
        reject({
          errors: [
            "The uploaded file contains only headers but no data. Please add at least one row.",
          ],
          headers,
        });
        return;
      }

      // ‚úÖ Extract headers from the first data row
      const fileHeaders = Object.keys(sheetData[0]);
      console.log("Parsed file headers:", fileHeaders);

      // üîç **Step 1: Find Matching Operation Based on Headers**
      let matchedOperation = null;
      for (const [key, value] of Object.entries(config.MassUpload)) {
        if (value.fileHeaders.every((header) => fileHeaders.includes(header))) {
          matchedOperation = key;
          console.log("Matched operation:", matchedOperation);
          break;
        }
      }
      if (!matchedOperation) {
        console.error("‚ùå No matching operation found for given headers.");
        reject({
          errors: ["File headers do not match any expected template."],
          headers,
        });
        return;
      }

      // ‚úÖ **Step 2: Validate Headers**
      const expectedHeaders = config.MassUpload[matchedOperation].fileHeaders;
      console.log("Expected Headers:", expectedHeaders);
      const mandatoryAttributes =
        config.MassUpload[matchedOperation].mandatoryAttributes;
      console.log("Mandatory Attributes:", mandatoryAttributes);

      // üîç **Step 3: Check for Missing Headers**
      const missingHeaders = expectedHeaders.filter(
        (header) => !fileHeaders.includes(header)
      );
      if (missingHeaders.length > 0) {
        console.error("‚ùå Missing headers:", missingHeaders);
        reject({
          errors: [`Missing required headers: ${missingHeaders.join(", ")}`],
          headers,
          mandatoryAttributes,
        });
        return;
      }

      // üîç **Step 4: Validate Mandatory Fields**
      const validationErrors = [];
      sheetData.forEach((row, index) => {
        mandatoryAttributes.forEach((field) => {
          if (!row[field] || row[field].toString().trim() === "") {
            validationErrors.push(
              `Row ${index + 2}: "${field}" is required but is empty`
            );
          }
        });

        // üîç **Step 5: Validate "Collaborative Space" Field**//

        const userCollabSpace = row["Collaborative Space"]
          ? row["Collaborative Space"].trim()
          : "";
        console.log(
          `[validateFile.js] Row ${index + 2}: User's Collaborative Space:`,
          userCollabSpace
        );

        if (!userCollabSpace) {
          validationErrors.push(
            `Row ${index + 2}: "Collaborative Space" is missing or empty.`
          );
        } else if (!collabSpaceTitles.includes(userCollabSpace)) {
          validationErrors.push(
            `Row ${
              index + 2
            }: "Collaborative Space" (${userCollabSpace}) is invalid. `
          );
        }
      });

      // Compare the value in "Collaborative Space" with the provided collabSpaceTitle

      // üî¥ **If there are validation errors, reject but return headers**
      if (validationErrors.length > 0) {
        console.log("Validation errors with mandatory attributes:", {
          errors: validationErrors,
          headers,
          mandatoryAttributes, // Make sure we're including this
        });
        reject({
          errors: validationErrors,
          headers,
          mandatoryAttributes, // Add this line
        });
        return;
      }

      // ‚úÖ **Final Step: Resolve with Headers and Sheet Data**
      console.log("‚úÖ File validation successful. No errors found.");
      resolve({
        headers,
        sheetData,
        validationErrors: [],
        mandatoryAttributes, // Make sure we're returning mandatoryAttributes
      });
    };

    // ‚ùå **Handle File Read Errors**

    reader.onerror = () => {
      console.error("‚ùå Error reading the file.");
      reject({ errors: ["File reading failed."], headers: [] });
    };
  });
};

export default validateFile;
